import { useState } from 'react'
import IconInput from './Input'
import IconSelect from './Select'
import PhoneImg from 'assets/icon/phone.png'
import MessageImg from 'assets/icon/message.png'
import PasswordImg from 'assets/icon/password.png'
import AnswerImg from 'assets/icon/answer.png'
import InvitationImg from 'assets/icon/invitation.png'
import PointImg from 'assets/img/point.png'
import { Button, makeStyles, styled } from '@material-ui/core'
import { H5_BREAKPOINT, PC_BREAKPOINT } from 'App'

const useStyles = makeStyles(
  (theme) => ({
    button: {
      color: '#130A11',
      fontSize: '24px',
      lineHeight: '44px',
      fontWeight: 'bold',
      fontFamily: 'PingFang SC',
      minWidth: '180px',
      height: '70px',
      marginLeft: '20px',
      background: '#EAC07B',
      border: '3px solid #EAAA40',
      boxShadow: '0px 0px 9px 1px rgba(255, 170, 24, 0.69)',
      borderRadius: '15px',
      [theme.breakpoints.down(H5_BREAKPOINT)]: {
        minWidth: '160px',
        height: '56px',
        marginLeft: '10px',
        fontSize: '20px',
        lineHeight: '35px',
      },
    },
    confirmButton: {
      fontSize: '36px',
      fontFamily: 'PingFang SC',
      fontWeight: 'bold',
      color: '#130A11',
      lineHeight: '44px',
      background: '#E9C07A',
      boxShadow: '0px 1px 14px 2px rgba(124, 78, 0, 0.69)',
      border: '1px solid #C2A26B',
      borderRadius: '35px',
      width: '80%',
      height: '70px',
      maxWidth: '424px',
      [theme.breakpoints.down(H5_BREAKPOINT)]: {
        fontSize: '30px',
        lineHeight: '35px',
      },
    },
    footer: {
      display: 'flex',
      justifyContent: 'center',
      position: 'relative',
      top: '8px',
      [theme.breakpoints.down(PC_BREAKPOINT)]: {
        background: `url("${PointImg}")`,
        backgroundSize: 'contain',
        backgroundRepeat: 'no-repeat',
        backgroundPosition: 'center',
      },
      marginBottom: '36px',
    },
  }),
  { index: 1 }
)

const Row = styled('div')({ marginBottom: '20px', display: 'flex', alignItems: 'center' })

export default function Form() {
  const classes = useStyles()
  const [, setFormData] = useState({})

  return (
    <div>
      <Row>
        <IconInput
          icon={PhoneImg}
          placeholder="请输入手机号码"
          onChange={(val) => setFormData((prev) => ({ ...prev, phone: val }))}
        />
      </Row>
      <Row>
        <IconInput
          icon={MessageImg}
          placeholder="请输入短信验证码"
          onChange={(val) => setFormData((prev) => ({ ...prev, message: val }))}
        />
        <Button variant="contained" className={classes.button}>
          获取验证码
        </Button>
      </Row>
      <Row>
        <IconInput
          icon={PasswordImg}
          placeholder="请输入密码"
          onChange={(val) => setFormData((prev) => ({ ...prev, password: val }))}
        />
      </Row>
      <Row>
        <IconInput
          icon={PasswordImg}
          placeholder="请再次输入密码"
          onChange={(val) => setFormData((prev) => ({ ...prev, passwordConfirm: val }))}
        />
      </Row>
      <Row>
        <IconSelect onChange={(val) => setFormData((prev) => ({ ...prev, question: val }))} />
      </Row>
      <Row>
        <IconInput
          icon={AnswerImg}
          placeholder="请输入答案"
          onChange={(val) => setFormData((prev) => ({ ...prev, answer: val }))}
        />
      </Row>
      <Row>
        <IconInput
          icon={InvitationImg}
          placeholder="请输入邀请码"
          onChange={(val) => setFormData((prev) => ({ ...prev, invitationCode: val }))}
        />
      </Row>
      <footer className={classes.footer}>
        <Button className={classes.confirmButton} variant="contained">
          提交注册
        </Button>
      </footer>
    </div>
  )
}
