import { styled, CssBaseline, useTheme, useMediaQuery } from '@material-ui/core'
import PcBg from 'assets/img/pc-bg.png'
import H5Bg from 'assets/img/h5-bg.png'
import MemberRegisterImg from 'assets/img/member-register.png'

import Form from 'components/Form'

export const H5_BREAKPOINT = 500
export const PC_BREAKPOINT = 720
const PC_MAX_WIDTH = 1440

const Main = styled('main')(({ theme }) => ({
  background: '#292131',
  height: '100vh',
  width: '100%',
  display: 'flex',
  flexDirection: 'row',
  overflowX: 'auto',
  [theme.breakpoints.down(PC_BREAKPOINT)]: {
    flexDirection: 'column',
  },
  [theme.breakpoints.up(PC_MAX_WIDTH)]: {
    justifyContent: 'center',
  },
  '::-webkit-scrollbar': {
    display: 'none',
  },
  '& .bg': {
    width: '100%',
    [theme.breakpoints.up(PC_MAX_WIDTH)]: {
      width: 'auto',
      height: '896px',
    },
  },
  '& .section': {
    [theme.breakpoints.up(PC_BREAKPOINT)]: {
      padding: '0px 8px',
      minWidth: '720px',
    },
    [theme.breakpoints.up(PC_MAX_WIDTH)]: {
      height: '896px',
    },
    '&.first': {
      [theme.breakpoints.up(PC_MAX_WIDTH)]: {
        textAlign: 'end',
      },
    },
    '&.second': {
      paddingTop: '60px',
      textAlign: 'center',
      [theme.breakpoints.down(PC_BREAKPOINT)]: {
        padding: '0px 40px',
        marginTop: '-120%', // based on height, not width
      },
      [theme.breakpoints.down(H5_BREAKPOINT)]: {
        padding: '0px 10px',
      },
    },
  },
}))

function App() {
  const theme = useTheme()
  const isPc = useMediaQuery(theme.breakpoints.up(PC_BREAKPOINT))

  return (
    <Main>
      <section className="section first">
        <img className="bg" src={isPc ? PcBg : H5Bg} alt="bg" />
      </section>

      <section className="section second">
        {isPc && (
          <img
            style={{ height: '48px', marginBottom: '28px' }}
            src={MemberRegisterImg}
            alt="member"
          />
        )}
        <Form />
      </section>
    </Main>
  )
}

function AppWrapper() {
  return (
    <>
      <CssBaseline />
      <App />
    </>
  )
}

export default AppWrapper
