import { Input, InputAdornment, makeStyles } from '@material-ui/core'
import { H5_BREAKPOINT } from 'App'

const useStyles = makeStyles(
  (theme) => ({
    root: {
      background: '#FFFFFF',
      border: '3px solid #D4B47C',
      borderRadius: '15px',
      padding: '0px 26px',
      width: '100%',
      height: '70px',
      [theme.breakpoints.down(H5_BREAKPOINT)]: {
        height: '56px',
        padding: '0px 12px',
      },
    },
    input: {
      color: '#999999',
      fontSize: '24px',
      lineHeight: '44px',
      fontWeight: 500,
      fontFamily: 'PingFang SC',
      [theme.breakpoints.down(H5_BREAKPOINT)]: {
        fontSize: '20px',
        lineHeight: '35px',
      },
    },
    adorment: {
      width: '40px',
      justifyContent: 'center',
      [theme.breakpoints.down(H5_BREAKPOINT)]: {
        width: '38px',
        '& > img': {
          transform: 'scale(0.8)',
        },
      },
    },
  }),
  { index: 1 }
)

interface Props {
  icon: any
  placeholder: string
  onChange: (val: string) => void
}

export default function IconInput(props: Props) {
  const classes = useStyles()

  return (
    <Input
      disableUnderline={true}
      classes={{ root: classes.root, input: classes.input }}
      onChange={(event) => props.onChange(event.target.value)}
      placeholder={props.placeholder}
      startAdornment={
        <InputAdornment position="start" className={classes.adorment}>
          <img src={props.icon} alt="icon" />
        </InputAdornment>
      }
    />
  )
}
